* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.container-fluid {
  position: absolute;
  /*border: 1px solid #050404;*/
  top: 20rem;
  justify-content: center;
  flex-direction: row;
  text-align: center;
  scroll-behavior: auto;
}

button {
  background-color: rgba(0, 0, 0, 0.103);
  border: none;
  /* border: 1px solid rgba(255, 255, 255, 0.171); */
  /* box-shadow: 0px 3px 10px rgb(255, 255, 255); */
  box-shadow: 0 0 0.7rem rgb(255, 255, 255);
  cursor: pointer;
  color: rgb(255, 255, 255);
  padding: .7rem 1.5rem;
  border-radius: 8px;
  cursor: pointer;
  font-family: "Poppins", sans-serif;
}
.button-category{
  top: 7rem;
  position: fixed;
  z-index: 1;
  width: 100%;
  background-color: black;
}
.button-category button:hover{
/* background-color:rgb(65, 16, 16); */
background: #ff3939;
transition-duration: 0.5s;
box-shadow: 0 0 1.2rem rgb(217, 116, 116);
}

.button-category button {
  display: relative;
  width: 115px;
  margin: 2rem;
}
.movie-app > .row{
  overflow-x: auto;
  flex-wrap: nowrap;
  display: grid;
  position: relative;
  left:15rem;
  justify-content: center;
  flex-direction: row;
  margin: 1 auto;
   grid-template-columns: repeat(5, minmax(200px, 1fr));
}


.image-container {
  box-shadow: 1px 1px 11px rgb(255, 255, 255);
  margin: 2rem;
  cursor: pointer;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  position: relative;
  flex-direction: row;
  zoom:1;
	transition: -webkit-transform 0.2s;
	transition: transform 0.2s;
	transition: transform 0.2s, -webkit-transform 0.2s;
  display: flex;
  flex-wrap: nowrap;
}

.image-container :hover{
  cursor: pointer;
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
  box-shadow: 2px 2px 15px rgb(255, 122, 122);
  transition-duration: 0.5s;
}

@media screen and (max-width: 940px) {
  .container-fluid {
    position: relative;
    /*border: 1px solid #050404;*/
    display: flex;
    padding:2rem;
    right:15rem;
    justify-content: center;
    flex-direction: row;
    text-align: center;
  }
  .movie-app > .row{
    overflow-y: auto;
    flex-wrap: nowrap;
    display: grid;
    position: flex;
    justify-content: center;
    flex-direction: row;
    margin: 1 auto;
    grid-template-columns: repeat(2, minmax(150px, 2fr));
  }
  .image-container {
    box-shadow: 2px 2px 10px rgb(255, 255, 255);
    margin: 0.75rem;
    cursor: pointer;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    position: relative;
    flex-direction: row;
    zoom:1;
    transition: -webkit-transform 0.2s;
    transition: transform 0.2s;
    transition: transform 0.2s, -webkit-transform 0.2s;
    display: flex;
    flex-wrap: nowrap;

  }
  .image-container img{
    height: 100%;
    display: inline-block;
    vertical-align:top; /*to remove unwanted whitespace */
    position: relative;
  }
  .image-container :hover{
    cursor: pointer;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  .button-category {
    display: auto;
    position: auto;
    top: 7.8rem;
    width: 100%;
    object-fit: contain;
  }
  .button-category button {
    margin: .8rem;
    width: 100px;
    text-align: center;
    justify-content:center;
  }

  .button-category button:hover{
    background: none;
    transition-duration: none;
    box-shadow: none;
    }

    .button-category button:active {
        background: #ff3939;
        box-shadow: 0 0 1.2rem rgb(217, 116, 116);
    }



}




